.who {
    padding: auto 134px;
    height: 1080px;
    background-color: rgb(0, 0, 0);
}

.who-p1 {
    margin-top: 0;
    color: white;
    font-size: 64px;
}

.who-p2 {
    text-align: right;
    margin-top: 0;
    color: white;
    font-size: 64px;
}

.who-div1 {
    position: relative;
    left: 134px;
    margin-bottom: 390px;
}

.who-div2 {
    position: relative;
    right: 134px;
    margin-bottom: 390px;
}

.square-who {
    float: right;
    margin-left: 300px;
    width: 11px;
    padding-bottom: 11px;
    background-color: rgb(255, 255, 255);
}